<template lang="en">
    <div>
      <canvas width=1000 height=500 id="canvas" ref="canv"></canvas>
    </div>
  </template>
<script>
  
import { draw,makeLine } from './Graph/functions.js'
draw;

export default {
  name: "graph_comp",
  data() {
    return {
      pipes: [],
    };
  },
  methods: {
    makePairData() {

      const success = (res) => (res.ok ? res.json() : Promise.resolve({}));

      const base = fetch(
        `https://api.binance.com/api/v3/klines?symbol=BTCUSDT&interval=1m`
      ).then(success);

      this.base = base;
      
    },
  },
  mounted() {
    
    this.makePairData();

    const canvas = this.$refs.canv;
    var ctx = canvas.getContext("2d");    
    this.ctx = ctx;
    
    // draw a candle function
    function drawCandle(time,o,c,h,l) {
      time, o, c, h, l;

      let s = 0; //start
      let a = 0; //append
      if(c<o){
        // long
        s = PtoY(c);
        a = PtoY(o) - PtoY(c);
        ctx.fillStyle = 'red';
        ctx.strokeStyle = 'red';
      } else {
        // short
        s = PtoY(o);
        a = PtoY(c) - PtoY(o);
        ctx.fillStyle = 'green';
        ctx.strokeStyle = 'green';
      }

      const xTime = PtoX(time)

      const _d = (1/ ((timeEnd - timeStart / (prices[1].t - prices[0].t))/100));
      const candleWidth = 9 * _d;

      // rectangle
      ctx.fillRect(xTime - candleWidth/2,s,candleWidth,a);

      // mÃ¨che
      ctx.lineWidth = candleWidth/5;
      ctx.beginPath();
      ctx.moveTo(xTime, PtoY(h));
      ctx.lineTo(xTime, PtoY(l));
      ctx.stroke(); 
    }

    // const data = fetch('./data/EURUSD1.csv').then(r => r.text())
    // console.log(data);

    // function loadFile(filePath) {
    //   var result = null;
    //   var xmlhttp = new XMLHttpRequest();
    //   xmlhttp.open("GET", filePath, false);
    //   xmlhttp.send();
    //   if (xmlhttp.status==200) {
    //     result = xmlhttp.responseText;
    //   }
    //   return result;
    // }

    // const data = loadFile("data/EURUSD1.csv");
    // console.log(data);



    // setup the price
    const prices = [
      // {'t':1,'o':50,'c':75,'h':80,'l':44},
      // {'t':2,'o':75,'c':80,'h':88,'l':70},
      // {'t':3,'o':80,'c':76,'h':82,'l':72},
      // {'t':4,'o':76,'c':91,'h':99,'l':74},
      // {'t':5,'o':91,'c':56,'h':91,'l':25},
      // {'t':6,'o':50,'c':75,'h':80,'l':44},
      // {'t':7,'o':75,'c':80,'h':88,'l':70},
      // {'t':8,'o':80,'c':76,'h':82,'l':72},
      // {'t':9,'o':76,'c':91,'h':99,'l':74},
      // {'t':10,'o':91,'c':56,'h':91,'l':25},
      // {'t':11,'o':50,'c':75,'h':80,'l':44},
      // {'t':12,'o':75,'c':80,'h':88,'l':70},
      // {'t':13,'o':80,'c':76,'h':82,'l':72},
      // {'t':14,'o':76,'c':91,'h':99,'l':74},
      // {'t':15,'o':91,'c':56,'h':91,'l':25},
      // {'t':16,'o':50,'c':75,'h':80,'l':44},
      // {'t':17,'o':75,'c':80,'h':88,'l':70},
      // {'t':18,'o':80,'c':76,'h':82,'l':72},
      // {'t':19,'o':76,'c':91,'h':99,'l':74},
      // {'t':20,'o':91,'c':56,'h':91,'l':25},
    ]

    let lp = 50
    let l = 5
    for (let i = 1; i < 100; i++) {
      prices.push({t:i,o:lp,c:lp+l,h:lp+2,l:lp+l-2})
      lp+=l;
      l = Math.random() * 10-5
    }

    // console.log(this.base);

    // this.base.then((value)=>{
    //   console.log(value);
    //   value.forEach(v => {
    //     prices.push({
    //       't':v[0],
    //       'o':v[1],
    //       'c':v[4],
    //       'h':v[2],
    //       'l':v[3],
    //     })
    //   });
    // })





    var priceStart = 50;
    var priceEnd = 100;

    var timeStart = 0;
    var timeEnd = 15;

    var mousePos = null;

    var info = {
      b1:{ // left button
        clicked: false, // is the key hold
        sp: {x:0, y:0}, // start position
      },
      b2:{ // right button
        clicked: false, // is the key hold
        sp: {x:0, y:0}, // start position
      }
    }
    



    // set the mouse position 
    document.addEventListener('mousemove',(e)=>{
      mousePos = getMousePos(canvas,e);
    })



    function MouseInRightMargin() {
      return info.b1.sp.x >= canvas.width-50;
    }

    const canMouseMove = ()=> {return info.b1.clicked && !MouseInRightMargin() }

    function PtoX(t) {
      let offset = 0;
      if(canMouseMove()){
        offset = XtoP(info.b1.sp.x - mousePos.x);
      } else {
        offset = 0;
      }
      const ts = timeStart + offset;
      const te = timeEnd + offset;
      const ratio = (t-ts) / (te-ts)
      return ratio * canvas.width
    }

    function PtoY(p) {
      let offset = 0;
      if(canMouseMove()){
        offset = YtoP(info.b1.sp.y - mousePos.y);
      } else {
        offset = 0;
      }
      const ps = priceStart - offset;
      const pe = priceEnd - offset;
      const ratio = 1-((p-ps) / (pe-ps))
      return ratio * canvas.height
    }

    function YtoP(p) {
      return (p / canvas.height) * (priceEnd-priceStart) ;
    }

    function XtoP(p) {
      return (p / canvas.width) * (timeEnd-timeStart);
    }

    function clear() {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
    }



    // annotate the graph
    function annotate(time,price,text) {
      time,price,text;
      ctx.fillStyle = 'black';
      ctx.font = "18px Arial";
      ctx.fillText(text,PtoX(time),PtoY(price));
    }
    annotate;

    // annotate to the right
    function annotateRight(price,text) {
      price,text;
      ctx.fillStyle = 'black';
      ctx.font = "18px Arial";
      ctx.fillText(text,canvas.width - 50,PtoY(price));
    }

    // annotate to down side
    function annotateDown(time,text) {
      time,text;
      ctx.fillStyle = 'black';
      ctx.font = "18px Arial";
      ctx.fillText(text,PtoX(time),canvas.height - 10);
    }

    function gridifyY(price) {
      price;
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 0.2;
      ctx.beginPath();
      ctx.moveTo(0, PtoY(price));
      ctx.lineTo(canvas.width, PtoY(price));
      ctx.stroke();
    }

    
    function getMousePos(canvas, evt) {
      var rect = canvas.getBoundingClientRect();
      return {
        x: evt.clientX - rect.left,
        y: evt.clientY - rect.top
      };
    }



    // var toDraw = {m:null};



    


    
    
    function tick() {

      // clear the graph
      clear();

      // const _d = (1/ ((timeEnd - timeStart / (prices[1].t - prices[0].t))/100));
      // console.log( _d );

      // Right margin
      const delta = priceEnd - priceStart
      for (let i = priceStart - delta; i < priceEnd + delta; i+=1) {
        const ii = Math.round(i)
        if(ii%10==0){
          annotateRight(ii,String(ii));
          gridifyY(ii)
        }
      }

      // Down margin
      for (let i = 0; i < 10; i++) {
        const ii = i
        annotateDown(ii,String(ii))
        // annotate(ii,priceEnd,String(ii));
      }

      // // dates
      // let date = new Date(timeStart).toLocaleString("fr", {timeZone: "America/New_York"})
      // annotateDown(timeStart,String(date))
      // annotateDown(timeEnd,String(timeEnd))

      // Draw the price
      prices.forEach(c => {
        drawCandle(c.t,c.o,c.c,c.h,c.l);
      });
      

      // when left mouse is held
      if(info.b1.clicked){
        makeLine(ctx,info.b1.sp.x,info.b1.sp.y,mousePos.x,mousePos.y)

        if(MouseInRightMargin()){
          let scroll = 0;
          if(mousePos.y - info.b1.sp.y >=0){
            scroll = 1;
          }else{
            scroll = -1;
          }

          const delta = priceEnd - priceStart;
          const percent = info.b1.sp.y/canvas.height;
          scroll*=delta*0.05*(-1)

          priceStart+=scroll*(1-percent);
          priceEnd-=scroll*(percent);
        }
      }

    }



    canvas.addEventListener('wheel', (event) => {
      let scroll = 0;
      if(event.deltaY >= 0){
        scroll = 1;
      } else {
        scroll = -1;
      }
      const delta = timeEnd - timeStart;
      const percent = mousePos.x/canvas.width;
      timeStart+=(delta*0.1*scroll)*percent;
      timeEnd-=(delta*0.1*scroll)*(1-percent);
    });
    document.addEventListener('contextmenu', event => event.preventDefault());


    
    // drag and drop for moving the price
    canvas.onmousedown = function(event) {
      if(event.which == 1){
        info.b1.clicked = true;
        info.b1.sp = getMousePos(canvas,event)
      } else {
        info.b2.clicked = true;
        info.b2.sp = getMousePos(canvas,event)
      }
    }

    canvas.onmouseup = function(event) {
      if(event.which == 1){
        info.b1.clicked = false;

        if(info.b1.sp.x <= canvas.width-50){

          const _x = XtoP(info.b1.sp.x - mousePos.x); 
          timeEnd+=_x;
          timeStart+=_x;
          
          const _y = YtoP(mousePos.y - info.b1.sp.y); 
          priceEnd+=_y;
          priceStart+=_y;
        }

      } else {
        info.b2.clicked = false;
      }
    }

    canvas.ondragstart = function(){
      return false;
    }


    setInterval(()=>{
      tick();

    },1000/120)

  },
};
</script>

<style scoped>
#canvas {
    outline-width: 5px;
    outline: auto;
    margin: 20px;
}
</style>











